import React, {useContext, useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import {Nav} from "react-bootstrap";
import styled from 'styled-components';

import './Detail.scss';
import {재고context} from './App.js'
import {CSSTransition} from "react-transition-group";
import { connect } from 'react-redux';

let 박스 = styled.div`
    padding : 20px;
`;
let 제목 = styled.h4`
    font-size : 25px;
    color : ${ props => props.색상 }
`;



function Detail(props) {

    let [alert, alert변경] = useState(true);
    let [inputData, inputData변경] = useState('');

    let [누른탭, 누른탭변경] = useState(0);
    let [스위치, 스위치변경] = useState(false);

    let 재고 = useContext(재고context);

    useEffect(()=>{
       let 타이머 =  setTimeout(()=> { alert변경(false) }, 2000);
       return () => { clearTimeout(타이머) } //timeout에 설정한 시간이 지나기전에 다른 페이지로 이동했을때 등의 버그방지
    },[alert]); //alert의 state가 변경될때만 useEffect가 실행될수있게하는 조건
    //useEffect 여러개 사용가능: 순차적으로 실행된다

    var btnMargin = {margin:'10px'};
    

    let { id } = useParams();
    let 찾은상품 = props.shoes.find(x => x.id == id );
    let history = useHistory();

    return (
        <div className="container">
            <박스>
                <제목>Detail</제목>
            </박스>

            {/* <input onChange={ (e) => inputData변경(e.target.value) }/> */}

            {
                alert == true
                ? 
                <div className="my-alert">
                    <p>재고가 얼마 남지 않았습니다.</p>
                </div>
                : null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src={찾은상품.image} width="100%"/>
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{찾은상품.title}</h4>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price}</p>
                    <Info 재고={props.재고}></Info>


                    <button className="btn btn-danger" onClick={ () => { 
                        props.재고변경([9,11,12]);
                        props.dispatch({ type : '항목추가' , data: {id:찾은상품.id, name:찾은상품.title, quan:1} });
                        history.push('/cart')
                    } }>주문하기</button>
                    <button className="btn btn-danger" style={btnMargin} onClick={ () => {
                        history.goBack();}}>뒤로가기</button>
                    {/* history.push('/어쩌구') -> '' 안에 데이터가 링크로 push되고 해당 페이지로 이동가능 */}
                </div>
            </div>

            <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={()=>{ 스위치변경(false); 누른탭변경(0) }}>Active</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={()=>{ 스위치변경(false); 누른탭변경(1) }}>Option 2</Nav.Link>
                </Nav.Item>
            </Nav>
            
            <CSSTransition in={스위치} classNames="wow" timeout={500}>
                <TabContent 누른탭={누른탭} 스위치변경={스위치변경}/>
            </CSSTransition>
           
        </div>
    )
}

function TabContent(props) {

    useEffect( ()=>{
        props.스위치변경(true);
    });

    if (props.누른탭 === 0){
        return <div>0번째 내용입니다</div>
      } else if (props.누른탭 === 1){
        return <div>1번째 내용입니다</div>
      } else if (props.누른탭 === 2){
        return <div>2번째 내용입니다</div>
      }
}


function Info (props)
{
    return (
        <p>재고: {props.재고[0]}</p>
    )
}


function state를props화(state){
    return {
        state : state.reducer,
        alertOpen : state.reducer2
    }
}
export default connect(state를props화)(Detail)

// export default Detail;